@font-face {
	font-family: Maison Neue;
	src: url('./assets/fonts/MaisonNeue-Book.ttf');
	/* IE9 Compat Modes */
}

@font-face {
	font-family: Ivy Mode Regular;
	src: url('./assets/fonts/IvyMode-Regular.ttf');
	/* IE9 Compat Modes */
}

@font-face {
	font-family: Ivy Mode Light Italic;
	src: url('./assets/fonts/IvyMode-LightItalic.ttf');
	/* IE9 Compat Modes */
}

@font-face {
	font-family: Eksell Display;
	src: url('./assets/fonts/Eksell-Display-Small.otf');
	/* IE9 Compat Modes */
}

$bg-color: #f1e5d5;

* {
	font-family: Maison Neue;
	letter-spacing: 0.5px;
}

html {
	background-color: white;
}

h1 {
	font-family: Ivy Mode Regular;
	letter-spacing: 0.3em;
	font-size: 3em;
	margin: 0;
}

h2 {
	font-family: Ivy Mode Light Italic;
	letter-spacing: 0.3em;
	font-style: italic;
	margin: 0;
	font-size: 1.3em;
	font-weight: 100;
	text-transform: uppercase;
}

h3 {
	margin: 0.2em 0;
}

form {
	margin: 2em 0;
}

select {
	padding: 5px;
}

.no-scroll {
	width: 100%;
	height: 100vh;
	overflow: hidden;
}

.App {
	width: 100%;
	height: 100vh;
	//padding: 3em;
	display: flex;
	flex-direction: column;
	overflow: hidden;
}

.App-header {
	text-align: center;
	//padding: 1em 0;
	padding: 3em;
	cursor: pointer;
	color: black;
	flex-shrink: 0;
	display: none;
}

.nav {
	position: fixed;
	right: 3em;
	top: 3em;
	font-size: 12px;
	width: 50px;
	cursor: pointer;
	z-index: 50;
}

.city-selection-wrapper {
	width: 100%;
	height: 100vh;
	display: flex;
	align-content: center;
	justify-content: center;
	flex-wrap: wrap;

	.city-selection {
		width: 400px;
		text-align: center;
		border: 1px solid black;
		padding: 3em 1em;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;

		h1 {
			font-size: 1.7em;
			margin-bottom: 1em;
		}

		p {
			padding: 1em;
			border: 1px solid black;
			width: 200px;
			margin: 0.5em;
			cursor: pointer;
			transition: 0.3s;
			font-size: 0.9em;
			box-shadow: 3px 3px 0 #000;

			&:hover {
				background: black;
				color: $bg-color;
				box-shadow: 3px 3px 0 #000;
			}
		}
	}
}

.city-wrapper {
	width: 100%;
	height: 100%;
	display: flex;
	flex-shrink: 2;
	position: relative;
}

.city-content-wrapper {
	width: 70%;
	height: 100%;
	border-right: 1px solid black;
	overflow-y: scroll;
}

.image-container {
	border-top: 1px solid black;
	width: 30%;
	height: 100%;
	padding: 30px;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: border-box;

	img {
		width: 100%;
	}
}

.category-wrapper {
	width: 100%;
	//border-bottom: 1px solid black;

	&:last-of-type {
		border-bottom: 1px solid black;
	}
}

.locations-wrapper {
	display: flex;
	width: 100%;
	flex-wrap: wrap;
}

.category-title {
	width: 100%;
	border-bottom: 1px solid black;
	border-top: 1px solid black;
	// display: flex;
	// align-items: center;
	padding: 0px;
	box-sizing: border-box;
}

.scrolling-banner-wrapper {
	width: 100%;
	text-transform: uppercase;
	/*padding: 10px 0;*/
	overflow: hidden;
	height: 35px;
	box-sizing: border-box;
}

.scrolling-text-wrapper {
	max-width: 100%;
	border-top: 1px solid black;
	border-bottom: 1px solid black;
	margin: 0 0 2em;
	overflow: hidden;
}

.scrolling-banner-text {
	white-space: nowrap;
	overflow: hidden;
	display: inline-block;
	animation: scroll 30s linear infinite;
	font-size: 14px;
	padding: 10px 0;
	height: 12px;
	/*padding-top: 5px;*/
}

.scrolling-banner-text p {
	display: inline-block;
	line-height: 14px;
	margin: 0;
	font-size: 14px;
	font-family: Ivy Mode Light Italic;
	letter-spacing: 0.3em;
}

.location-wrapper {
	// border-right: 1px solid black;
	// border-top: 1px solid black;
	// border-bottom: 1px solid black;
	flex: 0 1 33.3333%;
	width: 30%;
	padding: 20px;
	box-sizing: border-box;
	cursor: pointer;

	img {
		max-width: 100%;
	}

	p {
		font-size: 12px;
	}

	.location-title {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin-top: 20px;

		p {
			margin: 0
		}
	}

	.location-pic-filler {
		width: 100%;
		border: 1px solid black;
		background-color: black;
		padding-bottom: 75%;
	}
}

.map-container {
	width: 100%;
	height: 700px;
	display: flex;
	align-items: center;
	justify-content: center;
}

@keyframes scroll {
	0% {
		transform: translateX(0);
	}

	100% {
		transform: translateX(-50%);
	}
}

@keyframes slide {
	0% {
		transform: scaleX(0)
	}

	100% {
		transform: scaleX(1)
	}
}

@media only screen and (max-width: 1000px) {
	.city-content-wrapper {
		width: 50%;
	}

	.image-container {
		width: 50%;
	}

	.location-wrapper {
		flex: 0 1 50%;
		width: 50%;
	}
}

@media only screen and (max-width: 600px) {
	.city-content-wrapper {
		width: 100%;
		position: absolute;
		z-index: 2;
	}

	.image-container {
		width: 100%;
		position: absolute;
		padding: 50px;
	}
}